  font-weight: normal;
  font-style: normal;
}

@media screen and (-webkit-min-device-pixel-ratio: 0) {
  @font-face {
    font-family: 'BLOKK';
    src: url("fonts/BLOKKNeue-Regular.svg") format("svg");
  }
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

*, *:before, *:after {
  box-sizing: border-box;
}

::selection {
  color: #fdd692;
  background: #ec7357;
}

::-moz-selection {
  color: #fdd692;
  background: #ec7357;
}

html {
  font-size: 10px;
  font-family: 'Ubuntu', sans-serif;
}

body {
  position: relative;
  width: 100%;
  background: #fee9c4;
}

.wrapper {
  position: relative;
  height: 100%;
  width: 100%;
  max-width: 640px;
  padding: 18px 18px 80px;
  margin: 0 auto;
  text-align: center;
}

.wrapper p {
  font-size: 2rem;
  line-height: 1.4;
  text-align: left;
  font-family: 'BLOKK';
  color: #917269;
}

.site-header {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 4rem 0;
}

.site-footer {
  padding: 18px;
  text-align: right;
}

.site-footer a {
  font-size: 1.4rem;
  line-height: 1.4;
  color: #754f44;
}

.lib-name {
  display: inline-block;
  padding: 0;
  margin: 0;
  font-size: 3rem;
  line-height: .85;
  color: #ec7357;
}

.lib-name span {
  display: block;
  font-size: .5em;
  font-weight: normal;
  text-align: left;
  color: #754f44;
}

.back-btn {
  display: block;
  position: absolute;
  left: 0;
  width: 0;
  height: 0;
  text-indent: -10000px;
  border-top: 10px solid transparent;
  border-bottom: 10px solid transparent;
  border-right: 10px solid #754f44;
}

.content-container {
  height: 50vh;
  overflow: auto;
}

.content-container p {
  margin: 0;
}
